<template>
  <v-container>
    <v-breadcrumbs :items="items" large></v-breadcrumbs>
    <v-stepper v-model="e6" vertical>
    <v-stepper-step :complete="e6 > 1" step="1">
      动作一
      <small>记录数据</small>
    </v-stepper-step>

    <v-stepper-content step="1">
      <v-card color="blue-grey lighten-5" class="mb-12">
        <v-card-title>打开蒸汽阀给化料池水加热<br>控制化料温度60℃～80℃</v-card-title>
      </v-card>
      <v-btn color="primary" @click.stop="sta +=1 ; dialog = !dialog">下一步</v-btn>
      <v-btn text>异常</v-btn>
    </v-stepper-content>

    <v-stepper-step :complete="e6 > 2" step="2">动作二</v-stepper-step>

    <v-stepper-content step="2">
      <v-card color="blue-grey lighten-5" class="mb-12">
        <v-card-title>保温60.0℃～80.0℃ 4h 化料过程每1.0h巡检一次，查看水温及水位</v-card-title>
      </v-card>
      <v-btn color="primary" @click="sta +=1 ; e6 = sta">下一步</v-btn>
      <v-btn text>异常</v-btn>
    </v-stepper-content>

    <v-stepper-step :complete="e6 > 3" step="3">动作三</v-stepper-step>

    <v-stepper-content step="3">
      <v-card color="blue-grey lighten-5" class="mb-12">
        <v-card-title>每1.0h巡检一次，查看水温及水位</v-card-title>
      </v-card>
      <v-btn color="primary" @click="sta +=1 ; e6 = sta">下一步</v-btn>
      <v-btn text>异常</v-btn>
    </v-stepper-content>

    <v-stepper-step step="4">动作四</v-stepper-step>
    <v-stepper-content step="4">
      <v-card color="blue-grey lighten-5" class="mb-12">
        <v-card-title>观察水表面有无油状物</v-card-title>
      </v-card>
      <v-btn color="primary" href="/#/worklist">下一步</v-btn>
      <v-btn text>异常</v-btn>
    </v-stepper-content>
    </v-stepper>
    <v-dialog
      v-model="dialog"
      fullscreen hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar dark color="primary">
          <v-btn icon dark @click="dialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>提交数据</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn dark text @click="dialog = false ; e6 = sta"><v-icon>mdi-check</v-icon></v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-card-title>
          <span class="headline">数据</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" sm="6" md="4">
                <v-text-field label="化料温度(单位：℃)" required></v-text-field>
              </v-col>
<!--              <v-col cols="12" sm="6" md="4">-->
<!--                <v-text-field label="Legal middle name" hint="example of helper text only on focus"></v-text-field>-->
<!--              </v-col>-->
<!--              <v-col cols="12" sm="6" md="4">-->
<!--                <v-text-field-->
<!--                  label="Legal last name*"-->
<!--                  hint="example of persistent helper text"-->
<!--                  persistent-hint-->
<!--                  required-->
<!--                ></v-text-field>-->
<!--              </v-col>-->
<!--              <v-col cols="12">-->
<!--                <v-text-field label="Email*" required></v-text-field>-->
<!--              </v-col>-->
<!--              <v-col cols="12">-->
<!--                <v-text-field label="Password*" type="password" required></v-text-field>-->
<!--              </v-col>-->
              <v-col cols="12" sm="6">
                <v-select
                  :items="['0-17', '18-29', '30-54', '54+']"
                  label="Age*"
                  required
                ></v-select>
              </v-col>
<!--              <v-col cols="12" sm="6">-->
<!--                <v-autocomplete-->
<!--                  :items="['Skiing', 'Ice hockey', 'Soccer', 'Basketball', 'Hockey', 'Reading', 'Writing', 'Coding', 'Basejump']"-->
<!--                  label="Interests"-->
<!--                  multiple-->
<!--                ></v-autocomplete>-->
<!--              </v-col>-->
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="dialog = false">关闭</v-btn>
          <v-btn color="primary" @click="dialog = false ; e6 = sta" class="white--text">提交</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      e6: 1,
      dialog: false,
      sta: 1,
      items: [
        {
          text: '主页',
          disabled: false,
          href: '/#/'
        },
        {
          text: '代办工作',
          disabled: false,
          href: '/#/worklist'
        },
        {
          text: '24釜',
          disabled: true,
          href: 'breadcrumbs_link_2'
        }
      ]
    }
  }
}
</script>



