<template>
  <div>
    <div>{{data}}</div>
    <label>照相机</label>
    <input type="file" id='image' accept="image/*" capture='camera'>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        data: []
      }
    },
    mounted() {
      let that = this;
      var parms = new URLSearchParams();
      parms.append('json', '{"TYPE":"HMSeachOprUnit", "DATA":{"HMSOperatingUnitTable.Description":"","HMSOperatingUnitTable.OprUnitID":""}}');
      this.$axios.request({
        url: 'http://192.168.1.129:8020/WebService.asmx/execute',
        method: 'post',
        data: parms,
      }).then(function (data) {
        if (data.status == 200) {
          let res = that.transXml(data)
          console.log(res['Data'])

        }
      })
    }
  }
</script>



