<template>
  <v-container>
    <v-row>
      <v-col
        cols="12"
        xs="12"
        sm="6"
        md="6"
        lg="4"
        xl="3"
        v-for="item in data"
        :key="item.id"
      >
        <v-card
          class="my-2"
        >
          <v-card-text>
            <div>待办工作</div>
            <p class="display-1 text--primary">
              {{ item.title }}
            </p>
            <p>{{ item.current }}</p>
            <div class="text--primary">
              {{ item.myTask }}
            </div>
          </v-card-text>
          <v-card-actions>
            <v-btn
              class="success"
              color="indigo darken-1"
              @click="clickFn(item.id)"
            >
              开始工作
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      data: []
    }
  },
  methods: {
    clickFn: function (id) {
      this.$router.push({name: 'Worklistdetail', params: {id: id}})
    }
  },
  mounted() {
    let that = this;
    this.$axios.request({
      url: 'http://rap2.taobao.org:38080/app/mock/254201/api/worklist',
      method: 'get'
    }).then(function (data) {
      if (data.status == 200){
        that.data = data.data['data']
      }
    })
  }
}
</script>



