<template>
  <v-app id="inspire">
    <v-navigation-drawer
      v-model="drawer"
      :clipped="$vuetify.breakpoint.lgAndUp"
      app
    >
      <v-list dense>
        <template v-for="item in items">
          <v-row
            v-if="item.heading"
            :key="item.heading"
            align="center"
          >
            <v-col cols="6">
              <v-subheader v-if="item.heading">
                {{ item.heading }}
              </v-subheader>
            </v-col>
            <v-col
              cols="6"
              class="text-center"
            >
              <a
                href="#!"
                class="body-2 black--text"
              >EDIT</a>
            </v-col>
          </v-row>
          <v-list-group
            v-else-if="item.children"
            :key="item.text"
            v-model="item.model"
            :prepend-icon="item.model ? item.icon : item['icon-alt']"
            append-icon=""
          >
            <template v-slot:activator>
              <v-list-item-content>
                <v-list-item-title>
                  {{ item.text }}
                </v-list-item-title>
              </v-list-item-content>
            </template>
            <v-list-item
              v-for="(child, i) in item.children"
              :key="i"
              link
            >
              <v-list-item-action v-if="child.icon">
                <v-icon>{{ child.icon }}</v-icon>
              </v-list-item-action>
              <v-list-item-content>
                <v-list-item-title>
                  {{ child.text }}
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-group>
          <v-list-item
            v-else
            :key="item.text"
            link
            :to="item.path"
          >
            <v-list-item-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>
                {{ item.text }}
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </template>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar
      :clipped-left="$vuetify.breakpoint.mdAndUp"
      app
      color="blue darken-3"
      dark
      elevate-on-scroll
    >
      <v-app-bar-nav-icon @click.stop="drawer = !drawer"/>
      <router-link to="/">
        <v-toolbar-title
        style="width: 80px"
        class="ml-0 pl-4"
      >
        <span class="white--text font-weight-bold">瑞联</span>
        </v-toolbar-title>
      </router-link>
      <v-text-field
        flat
        solo-inverted
        hide-details
        prepend-inner-icon="mdi-magnify"
        label="搜索"
        class="hidden-sm-and-down"
      />
      <v-spacer/>
      <v-btn icon>
        <v-icon>mdi-apps</v-icon>
      </v-btn>
      <v-btn icon>
        <v-icon>mdi-bell</v-icon>
      </v-btn>
      <v-btn
        icon
        large
        @click.stop="dialog = !dialog"
      >
        <v-avatar
          size="32px"
          item
        >
          <v-img
            src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"
            alt="Vuetify"
          />
        </v-avatar>
      </v-btn>
    </v-app-bar>
    <v-content>
      <v-divider/>
      <router-view/>
    </v-content>
    <v-dialog
      v-model="dialog"
      width="800px"
    >
      <v-card>
        <v-card-title class="lighten-2">
          登录
        </v-card-title>
        <v-container>
          <v-row class="mx-2">
            <!--                        <v-col-->
            <!--                                class="align-center justify-space-between"-->
            <!--                                cols="12"-->
            <!--                        >-->
            <!--                            <v-row-->
            <!--                                    align="center"-->
            <!--                                    class="mr-0"-->
            <!--                            >-->
            <!--                                <v-avatar-->
            <!--                                        size="40px"-->
            <!--                                        class="mx-3"-->
            <!--                                >-->
            <!--                                    <img-->
            <!--                                            src="//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png"-->
            <!--                                            alt=""-->
            <!--                                    >-->
            <!--                                </v-avatar>-->
            <!--                                <v-text-field-->
            <!--                                        placeholder="Name"-->
            <!--                                />-->
            <!--                            </v-row>-->
            <!--                        </v-col>-->
            <!--                        <v-col cols="6">-->
            <!--                            <v-text-field-->
            <!--                                    prepend-icon="mdi-account-card-details-outline"-->
            <!--                                    placeholder="Company"-->
            <!--                            />-->
            <!--                        </v-col>-->
            <!--                        <v-col cols="6">-->
            <!--                            <v-text-field-->
            <!--                                    placeholder="Job title"-->
            <!--                            />-->
            <!--                        </v-col>-->
            <v-col cols="12">
              <v-text-field
                prepend-icon="mdi-account"
                placeholder="账号"
              />
            </v-col>
            <v-col cols="12">
              <v-text-field
                type="password"
                prepend-icon="mdi-account-key"
                placeholder="密码"
              />
            </v-col>
            <!--                        <v-col cols="12">-->
            <!--                            <v-text-field-->
            <!--                                    prepend-icon="mdi-text"-->
            <!--                                    placeholder="Notes"-->
            <!--                            />-->
            <!--                        </v-col>-->
          </v-row>
        </v-container>
        <v-card-actions>
          <!--                    <v-btn-->
          <!--                            text-->
          <!--                            color="primary"-->
          <!--                    >More-->
          <!--                    </v-btn>-->
          <v-spacer/>
          <v-btn
            color="primary"
            @click="dialog = false"
          >登录
          </v-btn>
          <v-btn
            text
            @click="dialog = false"
          >取消
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
export default {
  props: {
    source: String
  },
  data: () => ({
    dialog: false,
    drawer: null,
    items: [
      {icon: 'mdi-contacts', text: 'Contacts', path: '/login'},
      {icon: 'mdi-history', text: 'Frequently contacted', path: '/worklist'},
      {icon: 'mdi-content-copy', text: 'Duplicates'},
      {
        icon: 'mdi-chevron-up',
        'icon-alt': 'mdi-chevron-down',
        text: 'Labels',
        model: false,
        children: [
          {icon: 'mdi-plus', text: 'Create label'}
        ]
      },
      {
        icon: 'mdi-chevron-up',
        'icon-alt': 'mdi-chevron-down',
        text: 'More',
        model: false,
        children: [
          {text: 'Import'},
          {text: 'Export'},
          {text: 'Print'},
          {text: 'Undo changes'},
          {text: 'Other contacts'}
        ]
      },
      {icon: 'mdi-message', text: 'Send feedback'},
      {icon: 'mdi-help-circle', text: 'Help'},
      {icon: 'mdi-cellphone-link', text: 'App downloads'},
      {icon: 'mdi-keyboard', text: 'Go to the old version'}
    ]
  })
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



