<template>
  <div class="text-center" style="margin-top: 16px">
    <v-parallax
      dark
      style="height: 450px"
      src="https://cdn.vuetifyjs.com/images/backgrounds/vbanner.jpg"
    >
      <v-row
        align="center"
        justify="center"
      >
        <v-col class="text-center" cols="12">
          <h1 class="display-1 font-weight-bold mb-4">{{title}}</h1>
          <h4 class="font-weight-bold">{{content}}</h4>
        </v-col>
      </v-row>
    </v-parallax>
    <v-spacer style="margin: 20px 0px"></v-spacer>
    <router-link to="/worklist">
      <div class="d-flex d-sm-none">
        <v-btn x-large rounded color="primary" dark block>开始</v-btn>
      </div>
    </router-link>

  </div>
</template>

<script>
export default {
  data () {
    return {
      title: '西安瑞联',
      content: '安全管理系统'
    }
  }
}
</script>



